import React, {useEffect, useRef, useState} from 'react';
import {Image, Button, Modal} from 'antd';
import LightningMcQueen from '../images/Lightning_McQueen.png';
import hizliyim from '../sounds/BenHzlym.mp3';

const McquennPage = ({ handleYesOption }) => {
    const [buttonPosition, setButtonPosition] = useState({ x: 0, y: 0 });
    const [isVisible, setIsVisible] = useState(false); // Modal'in grnrln kontrol etmek iin state

    const handleMouseEnter = () => {
        const randomX = Math.random() * (window.innerWidth - 200); // Pencere genilii iinde rastgele X pozisyonu
        const randomY = Math.random() * (window.innerHeight - 200); // Pencere ykseklii iinde rastgele Y pozisyonu
        setButtonPosition({ x: randomX, y: randomY });
    };

    const handleButtonClick = () => {
        setIsVisible(true); // Butona basldnda modal' grnr yap
    };

    const handleModalClose = () => {
        setIsVisible(false); // Modal kapatldnda modal' grnmez yap
        handleYesOption();
    };

    const audioRef = useRef(null);

    useEffect(() => {
        audioRef.current.play();
    }, []);

    return (
        <div style={{display: 'flex', flexDirection: 'column', alignItems: 'center', height: '100vh'}}>
            <audio ref={audioRef} autoPlay>
                <source src={hizliyim} type="audio/mpeg"/>
                Taraycnz audio elementini desteklemiyor.
            </audio>
            <div style={{marginBottom: '20px'}}> {/* Resim */}
                <Image
                    style={{maxWidth: '90vw', maxHeight: '70vh', objectFit: 'contain'}}
                    src={LightningMcQueen}
                    alt="mcquenn"
                    preview={false}
                />
            </div>
            <h1> Bir Kahve er Miyiz ?</h1>
            <div style={{
                display: 'flex',
                justifyContent: 'space-between',
                width: '90vw',
                maxWidth: '500px'
            }}> {/* Evet ve Hayr Butonlar */}
                <Button
                    type="danger"
                    style={{width: '100px', background: "red"}}
                    onClick={handleButtonClick}
                >
                    Evet
                </Button>
                <Button
                    type="danger"
                    style={{
                        width: '100px',
                        position: buttonPosition.x === 0 ? 'relative' : 'absolute',
                        left: buttonPosition.x + 'px',
                        top: buttonPosition.y + 'px',
                        transition: 'top 0.3s ease-in-out',
                        background: "red"
                    }}
                    onMouseEnter={handleMouseEnter}
                    onClick={handleMouseEnter}
                >
                    Hayr
                </Button>
            </div>
            <Modal
                title="Mkemmel!"
                visible={isVisible} // Modal'in grnrl
                onCancel={handleModalClose} // Modal kapatlma ilevi
                footer={null} // Footer' kapatn, gerekiyorsa dzenleyebilirsiniz
            >
                <p>
                    Ozaman nereye gidiyoruz??? :)
                </p>
            </Modal>
        </div>
    );
};

export default McquennPage;
